import {fireEvent, render, waitFor} from '@testing-library/react-native';

global.crypto = {
  getRandomValues: function (buffer: any[]) {
    for (var i = 0; i < buffer.length; i++) {
      buffer[i] = Math.floor(Math.random() * 256);
    }
    return buffer;
  },
};
import Base from '../../Apps/Base';
import {loadFeature, defineFeature} from 'jest-cucumber';
import {QuizContext} from './../../Apps/QuizContext';

const feature = loadFeature('./features/base.feature');

defineFeature(feature, test => {
  const mockQuizContext = {
    score: 7,
    setScore: jest.fn(),
    name: 'chandu',
    setName: jest.fn(),
    email: 'chandra@chandu.com',
    setMail: jest.fn(),
    phone: '1234567890',
    setPhone: jest.fn(),
    selectedValue: 'ReactJS',
    setSelected: jest.fn(),
    trackScore: true,
    setTracker: jest.fn(),
  };
  test('user submits form with invalid mail', async ({given, when, then}) => {
    await waitFor(() => {
      const screen = render(
        <QuizContext.Provider value={mockQuizContext}>
          <Base />
        </QuizContext.Provider>,
      );
      expect(screen).toBeDefined();
    });
  });
});
